html,
body {
  margin: 0%;
  font-family: 'Nunito';
  color: var(--text-color);
  font-size: 1.25rem;
  background-color: var(--bg);
  height: 100%;
}

:root {
  --bg: #444444;
  --bg-dark: #292929;
  --text-color: #8E8E8E;
  --sidebar-size: calc(15% + 2rem);
  --border: 1px solid #444444;
  --border-radius: 8px;
}

.sidebar {
  margin: 0;
  padding: 0;
  width: var(--sidebar-size);
  height: 100%;
  background-color: var(--bg-dark);
  overflow: auto;
  position: fixed;
}

.sidebar-header {
  font-size: 1.5rem;
  border-bottom: var(--border);
  display: flex;
  align-items: center;
  padding: 1rem 1rem;
  justify-content: space-between;
  position: sticky;
}

.sidebar-content {
  display: flex;
  justify-content: space-evenly;
  flex-direction: column;
  padding: 1rem 1rem;
  position: sticky;
}

.sidebar-item {
  display: block;
}

.tab {
  position: relative;
  overflow: auto;
  padding: 1rem 0;
  margin-left: calc(var(--sidebar-size) + 1rem);
  margin-right: 1rem;
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
}

.container {
  background-color: var(--bg-dark);
  border: var(--border);
  border-radius: var(--border-radius);
}

.container-header {
  border-bottom: var(--border);
  padding: 1rem;
  min-height: 1.75rem;
}

.container-content {
  padding: 1rem;
}

.width-50 {
  flex: 1; /* Two in one row */
}

.width-100 {
  width: 100%;
}

.width-33 {
  flex: 2; /* Three in one row */
}

.login {
  background-color: var(--bg-dark);
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -55%);
  max-height: 350px;
  max-width: 350px;
  padding: 162px 75px;
}

.loginForm{
  color: var(--text-color);
}

.loginForm input {
  width: 100%;
  padding: 6px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  box-sizing: border-box;
}

.loginFormButton button{
  background-color: var(--bg);
  border: none;
  outline: none;
  margin-top: 30px;
  padding: 8px;
  width: 100%;
  font-size: 1.2rem;
  cursor: pointer;
}